@import url(https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jura:wght@300;400;500;600;700&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Common Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
  outline: none;
  font-family: "Jura", sans-serif;
}

html {
  font-size: 50%;
  scroll-behavior: smooth;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Page BG */
.page-bg {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
    url(/react-portfolio/static/media/bg.150cb94c.png) center no-repeat;
  background-size: cover;
}
/* End of Page BG */

/* Navigation */
.navbar{
  position:fixed;
  top: 2.5rem;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  padding-right: 3rem;
  transition: opacity 0.5s, visibility 0.5s, -webkit-transform 0.5s;
  transition: opacity 0.5s, visibility 0.5s, transform 0.5s;
  transition: opacity 0.5s, visibility 0.5s, transform 0.5s, -webkit-transform 0.5s;
  z-index: 100;
}

.navbar-link{
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  margin: 0 2.5rem;
  cursor: pointer;
  text-shadow: 0 0.3rem 0.5rem #000;
}

.menu-icon{
  position: fixed;
  width: 12rem;
  height: 7rem;
  top: 0;
  right: 6rem;
  flex-direction: column;
  opacity: 0;
  visibility: hidden;
  transition: opacity .2s;
  z-index: 100;
  cursor: pointer;
}

.menu-icon-line{
  width: 4rem;
  height: .1rem;
  background-color: #fff;
  margin: 0.5rem 0;
  box-shadow: 0 .3rem .5rem #000;
}

.show-menu-icon{
  opacity: 1;
  visibility: visible;
}

.hide-navbar{
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translateX(10rem);
          transform: translateX(10rem);
}
/* End of Navigation */

/* Mouse Circle */
.mouse-circle,
.mouse-dot {
  border-radius: 50%;
  position: fixed;
  z-index: 300;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0;
  pointer-events: none;
}

.mouse-circle {
  width: 6rem;
  height: 6rem;
  border: 0.1rem solid #FFE162;
  -webkit-animation: mouseCircleAnim 10s infinite linear;
          animation: mouseCircleAnim 10s infinite linear;
}

@-webkit-keyframes mouseCircleAnim {
  0% {
    width: 6rem;
    height: 6rem;
  }
  25% {
    width: 8rem;
    height: 8rem;
  }
  35% {
    width: 4rem;
    height: 4rem;
  }
  70% {
    width: 8rem;
    height: 8rem;
  }
  100% {
    width: 6rem;
    height: 6rem;
  }
}

@keyframes mouseCircleAnim {
  0% {
    width: 6rem;
    height: 6rem;
  }
  25% {
    width: 8rem;
    height: 8rem;
  }
  35% {
    width: 4rem;
    height: 4rem;
  }
  70% {
    width: 8rem;
    height: 8rem;
  }
  100% {
    width: 6rem;
    height: 6rem;
  }
}

.mouse-dot {
  width: 0.5rem;
  height: 0.5rem;
  background-color: #FF6464;
  -webkit-animation: mouseDotAnim 10s infinite linear;
          animation: mouseDotAnim 10s infinite linear;
}

@-webkit-keyframes mouseDotAnim {
  0% {
    width: 0.5rem;
    height: 0.5rem;
  }
  55% {
    width: 1.5rem;
    height: 1.5rem;
  }
  100% {
    width: 0.5rem;
    height: 0.5rem;
  }
}

@keyframes mouseDotAnim {
  0% {
    width: 0.5rem;
    height: 0.5rem;
  }
  55% {
    width: 1.5rem;
    height: 1.5rem;
  }
  100% {
    width: 0.5rem;
    height: 0.5rem;
  }
}
/* End of Mouse Circle */

.section-heading{
  font-size: 2rem;
  font-weight: 300;
  color: #fff;
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  left: 10rem;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 0;
  word-break: break-all;
  line-height: 4;
}

.section-1{
    position: relative;
    width: 100%;
    height: 100vh;
    margin-bottom: 10rem;
}

.section-3{
    width: 100%;
    height: 100%;
    padding: 10rem 0 10rem 0;
    position: relative;
}

.section-4{
  position: relative;
  width: 100%;
  padding: 10rem 0 10rem 0;
}


.section-5{
  position: relative;
  height: 80vh;
}

hr{
  border-style: dotted none none;
  border-color: #FF6464;
  border-width: 4px;
  position: relative;
  width: 3%;
  margin: auto;
}

/* Section 1 */
/* logo */
.logo{
    position: absolute;
    top: 2rem;
    left: 5rem;
    font-size: 2rem;
    text-transform: uppercase;
    color: white;
}
/* End of logo */

/* Animated-circles */
.main-circle{
    width: 55rem;
    height: 55rem;
    position: absolute;
    overflow: hidden;
    border-radius: 50%;
    transition: left 2s, top 2s;
}
    

.main-circle img{
    mix-blend-mode: hard-light; 
    -webkit-filter: brightness(1.18); 
            filter: brightness(1.18);
    position: relative;
    top: 0;
    left: 0;
}

.main-circle img.resize{
    width: 130%;
    height: 130%;
}

.circles{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
}

.circle{
    width: 22rem;
    height: 22rem;  
    border-radius: 50%;
    background-color: red;
    align-self: center;
    justify-self: center;
    opacity: .5;
    position: relative;
    transition: left 2s, top 2s;
    top: 0;
    left: 0;
}

.circle-1{
    background: url(/react-portfolio/static/media/circle-1-img.7aa62f1e.jpg) center no-repeat;
    background-size: cover;
    background-clip: content-box;
    border: .5rem dotted #FF6464;
    margin-left: -10rem;
}

.circle-2{
    background: url(/react-portfolio/static/media/circle-2-img.3a8fd210.jpg) center no-repeat;
    background-size: cover;
    background-clip: content-box;
    border: .5rem dashed #FFE162;
    margin-right: -10rem;
}

.circle-3{
    background: url(/react-portfolio/static/media/circle-3-img.0807b67f.jpg) center no-repeat;
    background-size: cover;
    background-clip: content-box;
    border: 1rem double #EEEEEE;
    margin-left: -10rem;
}

.circle-4{
    background: url(/react-portfolio/static/media/circle-4-img.195613f3.jpg) center no-repeat;
    background-size: cover;
    background-clip: content-box;
    border: .7rem double #91C483;
    margin-right: -10rem;
}


/* End of animated-circles */

/* Featured-TExt */
.featured-text{
    font-size: 2rem;
    color: white;
    text-transform:uppercase;
    width: 0;
    word-wrap: break-word;
    line-height: 3.4;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.featured-text-1{
    margin: 0 auto 0 10rem;
}
.featured-text-2{
    margin: 0 10rem 0 auto;
}
/* End of Featured-TExt */

.main-btn{
    position: absolute;
    bottom: 3rem;
    width: 13rem;
    height: 13rem;
    background-color:  #91C483;
    border-radius: 50%;
    border: .2rem dotted     #EEEEEE;
    padding: .2rem;
    overflow: hidden;
    cursor: pointer;
}
.main-btn-content{
    width: 100%;
    height: 100%;
    border: .4rem dotted #FF6464;
    border-radius:  50%;
    -webkit-transform: rotate(40deg);
            transform: rotate(40deg);
    transition: border 0.2s, -webkit-transform 0.2s;
    transition: transform 0.2s, border 0.2s;
    transition: transform 0.2s, border 0.2s, -webkit-transform 0.2s;
}

.main-btn:hover .main-btn-content{
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
    border: 0.4rem dotted #91C483;
    transition: border 0.2s, -webkit-transform 0.2s 0.5s;
    transition: transform 0.2s 0.5s, border 0.2s;
    transition: transform 0.2s 0.5s, border 0.2s, -webkit-transform 0.2s 0.5s;
}

.ripple {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #FF6464;
    border-radius: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-animation: rippleAnim 0.5s forwards;
            animation: rippleAnim 0.5s forwards;
}
@-webkit-keyframes rippleAnim {
    0%{
        width: 0;
        height: 0;
    }
    100%{
        width: 400%;
        height: 400%;
    }
}
@keyframes rippleAnim {
    0%{
        width: 0;
        height: 0;
    }
    100%{
        width: 400%;
        height: 400%;
    }
}

.main-btn-line{
    position: absolute;
    top: 4rem;
    width: 3rem;
    height: 1.5rem;
    border: .1rem solid white;
}

.main-btn-line:nth-child(1){
    -webkit-transform: rotateZ(-45deg) translateX(-0.3rem);
            transform: rotateZ(-45deg) translateX(-0.3rem);
    -webkit-transform-origin: left center;
            transform-origin: left center;
   
}

.main-btn-line:nth-child(2){
    -webkit-transform: rotateZ(45deg) translateX(.3rem);
            transform: rotateZ(45deg) translateX(.3rem);
    -webkit-transform-origin: right center;
            transform-origin: right center;
    
}

.main-btn-line:nth-child(3){
    -webkit-transform: rotateZ(90deg) translateX(.5rem);
            transform: rotateZ(90deg) translateX(.5rem);
   
}



.main-btn span{
    position: absolute;
    bottom: 3rem;
    color: #fff;
    font-size: 1.1rem;
    font-weight: 600;
    text-transform: uppercase;
}
/* End of Section 1 */

.projects{
    flex-wrap: wrap;
}


/* Big Project Image*/
.project-img-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 0;
    background-color: rgba(0,0,0,0.9);
    overflow: hidden scroll;
    -webkit-animation: imgWrapperAnim 1s forwards;
            animation: imgWrapperAnim 1s forwards;
}

@-webkit-keyframes imgWrapperAnim {
    0%{
        height: 0 ;
    }
    100%{
        height: 100%;
    }
}

@keyframes imgWrapperAnim {
    0%{
        height: 0 ;
    }
    100%{
        height: 100%;
    }
}

.project-img{
    width: 100%;
    border-radius: 0.5rem;
    padding: 7rem 20rem;
    opacity: 0.9;
    -webkit-transform-origin: top center;
            transform-origin: top center;
    -webkit-animation: imgAnim 1s 1s both;
            animation: imgAnim 1s 1s both;
}

@-webkit-keyframes imgAnim{
    0%{
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1)
    }
}

@keyframes imgAnim{
    0%{
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1)
    }
}
/* End of  Big Project Image*/
/*  Project Hide Button*/
.project-hide-btn{
    position: fixed;
    top: 5rem;
    right: 5rem;
    background-color: transparent;
    border: none;
    color: #fff;
    font-size: 2rem;
    letter-spacing: 0.1rem;
    cursor:pointer;
    z-index: 10;
    -webkit-transform: scale(0);
            transform: scale(0);
    visibility: hidden;
}

.change.project-hide-btn{
    -webkit-transform: scale(1);
            transform: scale(1);
    visibility: visible;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

/* End of Project hide Button*/


.project{
    width: 40rem;
    height: 45rem;
    overflow: hidden;
    margin: 5rem;
    background-color: black;
    display: flex;
    justify-content: center;
    position: relative;
    border: .2rem dotted #91C483;
    border-radius: .5rem;
    cursor: pointer;
    -webkit-perspective: 20rem;
            perspective: 20rem;
}

.project img{
    width: 90%;
    object-fit: cover;
    position: absolute;
    top: 2rem;
    opacity: 0.5;
    transition: top 4s 0.2s, opacity 0.2s;
}

@-webkit-keyframes oddImageAnim {
    0%{
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
    }
    25%{
        -webkit-transform:rotateY(-2deg) rotateX(2deg);
                transform:rotateY(-2deg) rotateX(2deg);
    }
    50%{
        -webkit-transform:rotateY(2deg) rotateX(-2deg);
                transform:rotateY(2deg) rotateX(-2deg);
    }
    100%{
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
    }
}

@keyframes oddImageAnim {
    0%{
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
    }
    25%{
        -webkit-transform:rotateY(-2deg) rotateX(2deg);
                transform:rotateY(-2deg) rotateX(2deg);
    }
    50%{
        -webkit-transform:rotateY(2deg) rotateX(-2deg);
                transform:rotateY(2deg) rotateX(-2deg);
    }
    100%{
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
    }
}
.project:nth-child(odd):hover img{
    -webkit-animation: oddImageAnim 4s 0.2s;
            animation: oddImageAnim 4s 0.2s;
    opacity: 1;
}

@-webkit-keyframes evenImageAnim {
    0%{
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
    }
    25%{
        -webkit-transform:rotateY(2deg) rotateX(-2deg);
                transform:rotateY(2deg) rotateX(-2deg);
    }
    50%{
        -webkit-transform:rotateY(-2deg) rotateX(2deg);
                transform:rotateY(-2deg) rotateX(2deg);
    }
    100%{
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
    }
}

@keyframes evenImageAnim {
    0%{
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
    }
    25%{
        -webkit-transform:rotateY(2deg) rotateX(-2deg);
                transform:rotateY(2deg) rotateX(-2deg);
    }
    50%{
        -webkit-transform:rotateY(-2deg) rotateX(2deg);
                transform:rotateY(-2deg) rotateX(2deg);
    }
    100%{
        -webkit-transform: rotateY(0);
                transform: rotateY(0);
    }
}
.project:nth-child(even):hover img{
    -webkit-animation: evenImageAnim 4s 0.2s;
            animation: evenImageAnim 4s 0.2s;
    opacity: 1;
}
